.pageHeader {
  background: $color-white;
  padding: 16px 32px 16px;
  margin-bottom: 24px;
}

.title {
  margin-bottom: 0px;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  font-size: 20px;
}

.subTitle {
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  font-weight: 400;
  margin-top: 16px;
}

.row {
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  align-items: center;
}

.title,
.content {
  flex: auto;
}

.title,
.subTitle {
  flex: 1 0 100%;
}

.extraContent,
.main {
  flex: 0 1 auto;
}

.extraContent {
  margin-left: 88px;
  min-width: 282px;
}

@media screen and (max-width: 768px) {
  .row {
    display: block;
  }
  .extraContent {
    margin-left: 16px;
  }
}
